:root {
    /**
     * Стандартная шрифтовая гарнитура для всех компонентов.
     */
    --font: 'Roboto', sans-serif;

    /**
     * Облегченная толщина шрифта. Может быть использована для
     * снижения визуальной массы текста и только при больших
     * кеглях. Например, используется в заголовках.
     *
     * Не используйте на малых и стандартных кеглях: на неретина
     * дисплеях сильно снижается читаемость.
     */
    --font-weight-light: 100;

    /**
     * Стандартная толщина шрифта для всех компонентов.
     */
    --font-weight: 300;

    /**
     * Толщина шрифта, используемая для добавления акцента.
     * Например, используется в меню, чтобы выделять заголовки
     * меню от пунктов меню.
     */
    --font-weight-accent: 400;

    /**
     * Толщина шрифта для сильного акцента. Например,
     * используется в акцентных кнопках.
     */
    --font-weight-heavy-accent: 500;

    /**
     * Самый маленький размер шрифта. Используется для самой второстепенной
     * информации, например, в подвале страницы.
     */
    --font-size-xs: 12px;

    /**
     * Маленький размер шрифта. Используйется для снижения акцента.
     */
    --font-size-s: 13px;

    /**
     * Стандартный размер шрифта.
     */
    --font-size-m: 16px;

    /**
     * Большой размер шрифта.
     */
    --font-size-l: 18px;

    /**
     * Экстра большой размер шрифта.
     */
    --font-size-xl: 22px;

    /**
     * Двойной экстра размер шрифта. Используется для особых акцентов,
     * например, в увеличенных отдельно стоящих ссылках.
     */
    --font-size-2xl: 30px;

    /**
     * Тройной экстра размер шрифта. Используется в крупных заголовках.
     */
    --font-size-3xl: 36px;

    /**
     * Четверной экстра размер шрифта. Используется в особо крупных заголовках.
     */
    --font-size-4xl: 60px;
}
