:root {
    /**
     * Цвет контента с особым акцентом для темы `alfa-on-white`.
     */
    --color-content-heavy-accent-alfa-on-white: rgba(178, 12, 0, 1);

    /**
     * Цвет контента с повышенным акцентом для темы `alfa-on-white`.
     * Используется, чтобы компенсировать потерю акцента для контента.
     *
     * Например для заголовков страниц, набранных тонким шрифтом, что делает
     * их "слишком светлыми" при использовании стандартного `--color-content-alfa-on-white`.
     */
    --color-content-accent-alfa-on-white: rgb(0, 0, 0);

    /**
     * Цвет контента для темы `alfa-on-white`.
     */
    --color-content-alfa-on-white: rgba(0, 0, 0, .9);

    /**
     * Цвет второстепенного контента для `alfa-on-white`.
     * Например, используется на неактивных полях ввода.
     */
    --color-content-minor-alfa-on-white: rgba(0, 0, 0, .3);

    /**
     * Цвет фона компонентов для `alfa-on-white`.
     */
    --color-background-alfa-on-white: rgba(243, 243, 243, 1);

    /**
     * Цвет фона для контролов для `alfa-on-white`.
     */
    --color-background-control-alfa-on-white: rgba(255, 255, 255, .9);

    /**
     * Цвет фона для контролов при наведении для `alfa-on-white`.
     */
    --color-background-control-hovered-alfa-on-white: rgba(0, 0, 0, .08);

    /**
     * Цвет фона для контролов в фокусе для `alfa-on-white`.
     */
    --color-background-control-focused-alfa-on-white: rgba(0, 0, 0, .08);

    /**
     * Цвет фона для замороженных контролов для `alfa-on-white`.
     */
    --color-background-control-disabled-alfa-on-white: rgba(0, 0, 0, .08);

    /**
     * Цвет рамки для контролов в фокусе для `alfa-on-white`.
     */
    --color-border-control-focused-alfa-on-white: rgba(0, 0, 0, .9);

    /**
     * Цвет фона для контролов в выбранном состоянии для `alfa-on-white`.
     */
    --color-background-control-checked-alfa-on-white: rgba(0, 0, 0, .15);

    /**
     * Цвет рамки для контролов в выбранном состоянии для `alfa-on-white`.
     */
    --color-border-control-checked-alfa-on-white: rgba(0, 0, 0, .15);
}
